import myTabs from '@/components/myTabs/myWarnHistroy.vue'
import myPull from '@/static/js/myWarnHistroy.js'
import minActionSheet  from '@/components/comselect/comselect'
import request from '../../api/request.js'
import global from '../../static/js/global.js'
export default {
	components:{myTabs,myPull,minActionSheet},
	data() {
		return {
			refresh:false,
			userList: [],
			/* 显示遮罩 */
			showShade: false,
			/* 窗口尺寸 */
			winSize: {},
			/* 显示操作弹窗 */
			showPop: false,
			/* 弹窗按钮列表 */
			popButton: ["误报", "消音"],
			/* 弹窗定位样式 */
			popStyle: "",
			/* 选择的用户下标 */
			pickerUserIndex: -1,
			curType:'',
			total:'',
			page:1
		}
	},
	onShow(){
		if(this.refresh){
			this.refresh=false
			this.getListData();
			this.getWindowSize();
					






		}
	},
	onLoad() {
		this.getListData();
		this.getWindowSize();
	






	},
	methods: {
		/**
		 * @name 改变tab
		 * @param val 索引
		 */
		tapChange(val){
			this.initIndex=val
		},
		
		listTap(item) {
			var that=this
			if (this.showShade) {
				return;
			}
			this.$refs.as.handleShow({
				actions: [
				{
					name: '误报',
					icon: 'iconfont active',
					color: '#007aff',
					image: '/static/img/MiniGgasMonitor.png'
				},
				{
					name: '报修',
					icon: 'iconfont active',
					color: '#007aff',
					image: '/static/img/MiniSmokeTrans.png'
				}
			],
			success: (res) => {
			  switch (res.id) {
				// -1代表取消按钮
				case -1:
				  uni.showToast({
				  	title: res.name
				  });
				  break
				case 0:
				  var param = {
				  	devId:item.devId
				  }
				  request.apiGet('/toc/deviceWarn/update',param).then((res) =>{
				  	if(res.code == '0'){
				  		uni.showToast({
				  			title: '状态修改成功'
				  		});
				  		that.getListData()
				  	}
				  })
				  break
				case 1:
				 uni.navigateTo({
				 	url:"/pages/repair/repairEdit?item="+JSON.stringify(item)
				 })
				  break
			  }
			}
		  })
		},
		/* 获取列表数据 */
		getListData() {
			var that = this
			var param = {
				openId:'wx123456789',
				page:this.page,
				limit:10
			}
			global.showLoading()
			request.apiGet('/toc/deviceWarn/listWarn',param).then((res) =>{
				if(res.code == '0'){
					this.userList=this.userList.concat(res.data)
					this.total=res.total
					global.hideLoading()
				}
			})
		},
		scrolltolower(){
			if(this.userList.length<this.total){
				this.page++
				this.getListData()
			}
			
		},
		/* 获取窗口尺寸 */
		getWindowSize() {
			uni.getSystemInfo({
				success: (res) => {
					this.winSize = {
						"witdh": res.windowWidth,
						"height": res.windowHeight
					}
				}
			})
		},
		/* 长按监听 */
		onLongPress(e) {
			let [touches, style, index] = [e.touches[0], "", e.currentTarget.dataset.index];
		
			/* 因 非H5端不兼容 style 属性绑定 Object ，所以拼接字符 */
			if (touches.clientY > (this.winSize.height / 2)) {
				style = `bottom:${this.winSize.height-touches.clientY}px;`;
			} else {
				style = `top:${touches.clientY}px;`;
			}
			if (touches.clientX > (this.winSize.witdh / 2)) {
				style += `right:${this.winSize.witdh-touches.clientX}px`;
			} else {
				style += `left:${touches.clientX}px`;
			}
		
			this.popStyle = style;
			this.pickerUserIndex = Number(index);
			this.showShade = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.showPop = true;
				}, 10);
			});
		},
		/* 隐藏弹窗 */
		hidePop() {
			this.showPop = false;
			this.pickerUserIndex = -1;
			setTimeout(() => {
				this.showShade = false;
			}, 250);
		},
		/* 选择菜单 */
		pickerMenu(e) {
			let index = Number(e.currentTarget.dataset.index);
			console.log(`第${this.pickerUserIndex+1}个用户,第${index+1}个按钮`);
			// 在这里开启你的代码秀
			if(index==0)
			{
				uni.showToast({
					title:"误报处理"
				});
			}
			if(index==1)
			{
				uni.navigateTo({
					url:"/pages/repair/repairEdit"
				})
			}
			// uni.showToast({
			// 	title: `第${this.pickerUserIndex+1}个用户,第${index+1}个按钮`,
			// 	icon: "none",
			// 	mask: true,
			// 	duration: 600
			// });
			/* 
			 因为隐藏弹窗方法中会将当前选择的用户下标还原为-1,
			 如果行的菜单方法存在异步情况，请在隐藏之前将该值保存，或通过参数传入异步函数中
			 */
			this.hidePop();
		}
	},
	mixins:[myPull({})],
	
}
